.gold {
  position:relative;
  left:45%;
}

.gold img {
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg); 
  width:52px;
  float: left;
  position: relative;
  top: 10px;
  left: 6px;
}

.gold h2 {
  float: left; 
  margin-left:20px; 
  color:white;
  font-weight:200;
  font-size: 20px;
  position: relative;
  top: 6px;
}

@media(max-width: 540px){
  .gold{
    display: inline-grid;
  }
  .gold h2{
    width: 100%;
    margin-top: -8px;
    margin-left: 10px;
  }
  }


